body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header {
  color: #fdf3f3;
  font: 500 12px/120px "Roboto", sans-serif;
  overflow: hidden;
  /* padding: 16px 8px; */
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  background-image: -webkit-linear-gradient(0deg,#766dff 0%,#88f3ff 100%)
}
.main-logo{
  flex: 4;
  padding-left: 50px;
  font-size: 20px;
}
.hamburger-btn{
 display: none;
}
.nav-main{
  flex: 8;
  width: 100%;
}
.nav-bar{
  padding: 10px 20px 0px 20px;
}

/* .main{
  
} */
.home{
  margin-top : 0px;
  background-image: -webkit-linear-gradient(0deg,#766dff 0%,#88f3ff 100%);
  width: 100%;
  height: 500px;
  position : absolute
}
.box{
  margin-top: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: white;
}
#container{
  display: flex;
  flex-flow: column;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  width: 1000px;
  height: 750px;
  border-radius: 3px;
  z-index: 100;
  background-image: -webkit-linear-gradient(0deg,#ffffff 0%,#dfdfdf 100%)
}
#container .first-container{
  flex : 2;
  margin: 30px;
  width: 560px;
  z-index: 2;
}
#container .second-container{
  flex : 1;
  margin-right: 30px;
  width: 260px;
  z-index: 2;
}
.home-img{
  width: 500px;
  height: 700px;
  border-radius: 8px;
  z-index: inherit;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
@media only screen and (max-width: 768px) {
  .nav-main{
   display: none;
  }
  .nav-bar{
    display: none;
  }
  .hamburger-btn{
    flex: 0.17;
    display: block;
    font-size: 1.3rem;
    bottom: 0;
    height: 35px;
    right: 30px;
    color: whitesmoke;
    /* height: 80px; */
    align-items: stretch;
  }
  .hamburger-bar{
    width: 100%;
    height: 5px;
    background-color: black;
    margin-top: 4px;
    margin-bottom: 7px;
  }
  .close {
    position: absolute;
    right: 32px;
    top: 32px;
    width: 32px;
    height: 32px;
    opacity: 0.3;
  }
  .close:hover {
    opacity: 1;
  }
  .close:before, .close:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 33px;
    width: 2px;
    background-color: #333;
  }
  .close:before {
    transform: rotate(45deg);
  }
  .close:after {
    transform: rotate(-45deg);
  }
  .addTransiton{
    transform: translateX(170px);
    transition: transform 0.5s ease-in-out;
  }
  .removeTransition{
    transform: translateX(20px);
    transition: transform 0.5s ease-in-out;
  }
  .box .container{
    border : 1px solid black;
    width: 500px;
    height: 1000px;
    z-index: 2;
    border-radius: 3px;
    background-color: blanchedalmond;
  }
  .box .container .first-container{
    margin-left: 50px;
    margin-right: 50px;
    margin-top: 30px;
    z-index: 2;
  }
  .home-img{
    width: 400px;
    height: 500px;
  }
}